@function rem($px) {
    @return ($px / 16)+rem;
}

@mixin selectDefault {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
}

@mixin customSelect {
    width: 118px;
    flex: 0 0 118px;
    height: 100%;
    background: #fff;
    border-radius: 10px;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #1B2124;
    position: relative;

    select {
        border: none;
        padding: 0 14px;
        background: transparent;
        height: 100%;
        width: 100%;
        @include selectDefault;
    }

    svg {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 14px;

    }
}

@mixin elementAction {
    display: grid;
    grid-template-columns: 36px 36px;
    grid-gap: 16px;

    .like {

        &.active,
        &:active {
            border: 1px solid #D4D7DF;

            svg path {
                fill: #3D425A;
                stroke: #3D425A;
            }
        }
    }

    .option {
        position: relative;

        &.active,
        &:active {
            background: #F6F8F9;
            border: 1px solid #D4D7DF;

            path {
                stroke: #3D425A;
            }

        }

        &.active {
            .option-content {
                opacity: 1;
                pointer-events: visible;
            }
        }

        &-content {
            opacity: 0;
            pointer-events: none;
            position: absolute;
            height: fit-content;
            width: 150px;
            background: #FFFFFF;
            border: 1px solid #F3F3F3;
            box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            top: calc(100% + 5px);
            right: 0;
            padding: 14px;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            z-index: 2;
            .blue {
                color: #1F8FFF;
            }

            li:not(:last-child) {
                margin-bottom: 12px;
            }

        }
    }

    &>* {
        border-radius: 50%;
        height: 36px;
        border: 1px solid #F3F3F3;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all .3s ease;

        path {
            transition: all .3s ease;
        }

        &:hover {
            border: 1px solid #AAAFBE;

            svg path {
                fill: #3D425A;
                stroke: #3D425A;
            }
        }

        &.edit {
            &:hover {
                svg path {
                    fill: unset;
                    stroke: #3D425A;
                }
            }
        }
        &.search,
        &.video,
        &.call {
            &:hover {
                svg path {
                    fill: unset;
                }
            }
        }
    }
}

@mixin filter {

    &-item {
        &:not(:last-child) {
            margin-bottom: 26px;
        }

        &__title {
            display: flex;
            cursor: pointer;
            align-items: center;
            justify-content: space-between;
        }

        &__list {
            li {
                display: flex;
                cursor: pointer;
                align-items: center;
                justify-content: space-between;

                &.active {
                    .value {
                        color: #47D18C;
                    }
                }

                .value {
                    background: #F3F3F3;
                    border-radius: 10px;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: center;
                    color: #7F879E;
                    padding: 4px 10px;
                }
            }
        }

        &__flex {
            .tag {
                display: flex;
                flex-wrap: wrap;
                margin-top: -12px;

                &-item {
                    margin-top: 12px;
                    background: #F9F9F9;
                    border-radius: 12px;
                    padding: 8px 14px;
                    font-weight: 500;
                    font-size: 12px;
                    cursor: pointer;
                    line-height: 130%;
                    width: fit-content;
                    transition: all .3s ease;

                    &.active {
                        background: #BBE2C6;
                        color: #268D61;
                    }

                    &:not(:last-child) {
                        margin-right: 8px;
                    }
                }
            }
        }

        .filter-content {
            padding-top: 26px;
            display: none;

            &.active {
                display: block;
            }
        }

        .checkbox-item {
            @include checkbox;

            .checkbox {
                margin-right: 14px;
            }

            label {
                font-size: 14px;
                line-height: 160%;
                color: #7F879E;
            }
        }
    }

    li {
        &:not(:last-child) {
            margin-bottom: 16px;
        }
    }
}

@mixin filterCategory {
