        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        background: #F9F9F9;
        border-radius: 8px;
        margin-bottom: 22px;
        padding-right: 10px;

        &>svg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 14px;
        }

        input {
            flex: 1;
            padding-left: 36px;
            background: transparent;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #7F879E;

            @include placeholder {
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #7F879E;
            }
        }
    }

    &__list {
        li {
            &:not(:last-child) {
                margin-bottom: 18px;
            }

            &:not(.edit-mode) {
                margin-right: 12px;
            }

            display: flex;
            cursor: pointer;
            align-items: center;
            justify-content: space-between;

            &.active {
                .value {
                    color: #47D18C;
                }
            }

            .value {
                background: #F3F3F3;
                border-radius: 10px;
                font-weight: 500;
                font-size: 12px;
                line-height: 130%;
                text-align: center;
                color: #7F879E;
                padding: 4px 10px;
            }

            &.edit-mode {
                position: relative;

                .value {
                    display: none;
                }

                svg {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 14px;
                }

                .edit-input {
                    display: block;
                }
            }

            .edit-input {
                display: none;
                padding: 0 12px;
                height: 38px;
                background: #FFFFFF;
                box-shadow: 0px 4px 15px rgba(127, 135, 158, 0.15);
                border-radius: 8px;
                flex: 1
            }
        }
    }

    .checkbox-item {
        @include checkbox;

        .checkbox {
            margin-right: 14px;
        }

        label {
            font-size: 14px;
            line-height: 160%;
            color: #7F879E;
        }
    }

    .add-item {
        color: #1F8FFF;
        display: flex;
        align-items: center;
        margin-top: 16px;

        svg {
            margin-right: 16px;
        }

        input {
            display: none;
        }

        &.adding-mode {
            path {
                stroke {
                    color: $setBlack1;
                }
            }

            span {
                display: none;
            }

            input {
                display: block;
                padding-right: 12px;
                background: #FFFFFF;
                border-radius: 8px;
                flex: 1;
                color: #3D425A;

                @include placeholder {
                    color: #D4D7DF;
                }
            }
        }
    }
}

