@mixin radioItem {
    position: relative;
    width: 20px;
    height: 20px;
    margin-right: 14px;

    &:hover {
        .active {
            border-color: #47D18C;
        }
    }

    input {
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 2;
        cursor: pointer;
    }

    .active {
        transition: all .3s ease;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0.8;
        border: 1.5px solid #7F879E;
        border-radius: 50%;
    }

    input:checked+.active {
        background: #47D18C;
        border: 2px solid #47D18C;

        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 6px;
            height: 6px;
            background: #FFFFFF;
            border-radius: 50%;
        }
    }

    p {
        font-weight: 400;
        font-size: 14px;
        line-height: 160%;
        color: #1B2124;
    }
}

