@mixin checkbox {
    display: flex;
    align-items: center;

    label {
        margin: 0;
    }

    .checkbox {
        width: 20px;
        height: 20px;
        border: 1.5px solid #7F879E;
        border-radius: 6px;
        position: relative;
        margin-right: 10px;
        transition: all .3s ease;

        &:hover {
            border: 1.5px solid #47D18C;
        }
    }

    .custom-checkbox {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        z-index: 11;
        cursor: pointer;
    }

    .tick {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 10;
        opacity: 0;
        width: 22px;
        height: 22px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .custom-checkbox:checked+.tick {
        opacity: 1;
    }
}

