        &.no-img {
            position: relative;
            background: #B6DAFF;
            &::after {
                content: attr(data-letter);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                font-size: 16px;
                line-height: 160%;
                color: #FFFFFF;
            }
        }
        .status {
            position: absolute;
            width: 10px;
            height: 10px;
            right: 5px;
            top: 0px;
            background: #F59A74;
            border: 1px solid #FFFFFF;
            border-radius: 50%;

            &.online {
                background: #78C58C;
            }
        }

        img {
            border-radius: 50%;
        }
    }

    .h2 {
        margin-bottom: 2px;
    }
}

@mixin inputGroup {
    margin-bottom: 25px;

    @include max576 {
        margin-bottom: 20px;
    }

    label {
        display: block;
        margin-bottom: 12px;
        color: $black1;
        font-weight: 600;
    }

    .input-wrapper {
        position: relative;

        .error-text {
            position: absolute;
            top: calc(100% + 4px);
            left: 0;
            color: $red1;
            opacity: 0;
            pointer-events: none;
        }

        &.error {
            input {
                border-color: $red3;
                background: white;
                color: $red1;
            }

            input+svg {
                path {
                    stroke: $red1;
                }
            }
        }

        input {
            background: #F6F8F9;
            border-radius: 12px;
            border: none;
            width: 100%;
            height: 48px;
            color: $setBlack3;
            padding: 0 16px 0 48px;
            transition: all .3s ease;
            border: 2px solid transparent;

            @include max576 {
                height: 46px;
            }

            &:hover {
                background: $lightgrey1;
            }

            &:focus {
                background: $lightgrey3;
                border: 2px solid $green3;
            }

            &:active {
                background: white;
                border: 2px solid $grey3;
            }

            &:not(:placeholder-shown) {
                border: 2px solid $grey3;
            }

            @include placeholder {
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
                color: #7F879E;
            }
        }

        &>svg {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 16px;
        }

        .password_vision {
            left: unset;
            right: 16px;
            cursor: pointer;
        }
    }
}

@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }

    &:-moz-placeholder {
        @content;
    }

    &::-moz-placeholder {
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }
}

