
.navigation {
    margin-top: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    * {
        cursor: pointer;
    }
    .arrow {
        &:hover path {
            stroke: #47D18C;
        }
    }
    .pages-nav {
        margin: 0 44px;
        display: grid;
        grid-template-columns: repeat(4, 44px);
        grid-gap: 12px;
        @include max768 {
            margin: 0 36px;
        }
        &__item {
            width: 44px;
            height: 44px;
            border-radius: 8px;
            background: #F9F9F9;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            color: #7F879E;
            transition: all .3s ease;
            display: flex;
            align-items: center;
            justify-content: center;
            &:hover {
                color: #47D18C;
            }
            &.active {
                background: #47D18C;
                color: #FFFFFF;
            }
        }
    }
}