.auth-wrapper {
    &__inner {
        display: grid;
        grid-template-columns: 51% 49%;
        @include max768 {
            display: block;
        }
    }
    .auth-form {
        padding: 125px 60px;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 730px;
        margin-left: auto;
        width: 100%;
        @include max1200 {
            padding: 120px 40px;
        }
        @include max768 {
            padding: 60px 40px;
            min-height: unset;
        }
        @include max576 {
            padding: 50px 20px 30px;
        }
        h3 {
            margin-bottom: 16px;
            @include max768 {
                margin-bottom: 12px;
            }
        }
        &>p {
            color: $grey1;
            font-size: 12px;
        }
        form {
            padding: 52px 0 32px;
            color: #7F879E;
            @include max576 {
                padding: 38px 0 32px;
            }
            button {
                width: 100%;
                margin: 52px 0 25px;
                height: 54px;
                @include max576 {
                    height: 48px;
                    margin: 30px 0 20px;
                }
            }
            p { 
                @include max576 {
                    font-size: 12px;
                }
                a {
                    color: $blue2;
                    transition: all .3s ease;
                    &:hover {
                        color: #47D18C;
                    }
                    // &.green-href {
                    //     color: #47D18C;
                    // }
                }
            }
        }
        &+p {
            color: #7F879E;
        }
    }
    .input-group {
        @include inputGroup;
    }
    .forgot_password {
        color: #1F8FFF;
        text-align: end;
        display: block;
        transition: all .3s ease;
        &:hover {
            color: #47D18C;
        }
        @include max576 {
            font-size: 12px;
        }
    }
    .social {
        margin-top: 46px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-content: space-between;
        align-items: center;
        grid-gap: 15px;
        color: $black1;
        @include max1200 {
            grid-template-columns: repeat(2, 1fr);
        }
        @include max576 {
            grid-template-columns: 1fr;
            margin-top: 32px;
        }
        li a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 600;
            height: 52px;
            border: 1px solid #F3F3F3;
            border-radius: 12px;            
        }
        img {
            margin-right: 12px;
        }
    }


    .auth-info {
        // background:  #B1DFC9, url(../img/auth-bg.svg);
        background-image: url(../img/auth-bg.svg);
        background-position: left center;
        background-color: #B1DFC9;
        position: relative;
        padding: 51px 0 0 60px;
        display: flex;
        flex-direction: column;
        @include max1200 {
            padding: 60px 0 0 40px;
        }
        @include max576 {
            padding: 40px 0 0 20px;
            background: url(../img/auth-bgMin.webp);
        }
        .logo {
            margin-bottom: 48px;
            max-width: 730px;
            margin-right: auto;
            width: 100%;
            @include max576 {
                margin-bottom: 38px;
            }
            img {
                @include max576 {
                    max-width: 130px;
                    height: auto;
                }
            }
        }
        h3 {
            margin-bottom: 16px;
            text-transform: capitalize;
        }
        p {
            color: #2D2D2D;
            opacity: 0.6;  
            max-width: 485px;
        }
        .auth-slider {
            position: relative;
            padding-bottom: 46px;
            margin-right: 60px;
            max-width: 570px;
            margin-right: auto;
            width: 100%;
            overflow: hidden;
            margin-bottom: 50px;
            @include max576 {
                max-width: 325px;
            }
            .swiper-pagination {
                right: unset;
                left: 0;
                display: flex;
                justify-content: start;
                .swiper-pagination-bullet {
                    background: #7F879E;
                    &.swiper-pagination-bullet-active {
                        background: #242D3C;
                    }
                }
            }
        }
        .auth-img {
            margin-top: auto;
            display: flex;
            justify-content: end;
            @media (min-width: 1450px) {
                margin-top: 0;
                justify-content: start;
            }
        }
    }
}
