.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all .3s ease;
    opacity: 0;
    visibility: hidden;
    display: flex;
    justify-content: center;
    padding: 20px 0;
    &.open {
        opacity: 1;
        visibility: visible;
    }
    .badge {
        @include badge(#268D61);
        line-height: 27px;
        padding: 0 14px;
        width: fit-content;
        &.orange {
            @include badge(#F59A74);
        }
    }

    .select-badge {
        &.is-hiring {
            margin-left: auto;
            @include badgeSelect(#268D61);
        }
        &.is-hidden {
            margin-left: auto;
            @include badgeSelect(#F59A74);
        }
        &.is-available {
            margin-left: auto;
            @include badgeSelect(#268D61);
        }
    }
    .tooltip {
        cursor: pointer;
        position: relative;
        margin-left: 8px;
        &:hover {
            .tooltip-inner {
                display: flex;
            }
        }
        &-inner {
            display: none;
            transition: all .3s ease;
            width: 178px;
            height: fit-content;
            background: #1B2124;
            border-radius: 8px;
            top: calc(100% + 5px);
            font-weight: 600;
            left: 0;
            font-size: 12px;
            line-height: 160%;
            justify-content: center;
            color: #FFFFFF;
            position: absolute;
            padding: 14px;
            white-space: initial;
            z-index: 2;
            .text {
                color: white !important;
            }
            @include max768 {
                left: 0;
                transform: translateX(0);
            }
        }
    }
}

.p-close {
    svg {
        pointer-events: none;
    }
}

.layout {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 101;
    background: rgba(37, 42, 63, 0.4);

}

.popup-object {
    background: white;
    z-index: 102;
    display: none;
    height: fit-content;
    margin: auto;
    &.open {
        display: block;
    }
    .bottom-row {
        border-top: 1.5px solid #F3F3F3;
        padding: 24px 26px 27px;
        display: flex;
        align-items: center;
        justify-content: end;
        @include max576 {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 11px;
        }
        button {
            height: 44px;
            font-size: 12px;
        }
        .neutral {
            flex: 0 0 128px;
            margin-right: 11px;
            @include max576 {
                margin-right: 0;
            }
        }
        .secondary {
            flex: 0 0 151px;
            svg {
                margin-right: 8px;
            }
        }
        .empty {
            color: $red1;
        }
    }
}

