.applications-popup {
    max-width: 800px;
    width: calc(100% - 40px);
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    padding: 34px 30px 60px;
    @include max576 {
        padding: 24px 20px 34px;
    }
    .applications-close {
        @include closePopup;
    }
    .badge {
        @include badge(#268D61);
        margin-bottom: 24px;
        @include max768 {
            margin-bottom: 16px;
        }
    }
    .title {
        font-weight: 700;
        font-size: 22px;
        line-height: 130%;
        color: #1B2124;
        margin-bottom: 6px;
        @include max768 {
            font-size: 20px;
        }
        @include max576 {
            font-size: 16px;
        }
    }
    ul.info {
        margin-top: -6px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        li {
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            margin-top: 6px;
            display: flex;
            align-items: center;
            p {
                margin-left: 8px;
                span:first-child {
                    font-weight: 600;
                    color: #1B2124;
                }
            }
            svg {
                &.info-alt {
                    cursor: pointer;
                    margin-left: 8px;
                }
            }
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }
    ul.requirement {
        border: 1.5px solid #F3F3F3;
        border-radius: 12px;  
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        margin: 24px 0;
        font-weight: 400;
        font-size: 12px;
        line-height: 160%;
        overflow: hidden;
        color: #7F879E;
        @include max768 {
            grid-template-columns: 1fr 1fr;
        }
        @include max576 {
            grid-template-columns: 54% 46%;
        }
        p {
            &:not(:last-child) {
                margin-bottom: 5px;
            }
            &:last-child {
                display: flex;
                align-items: center;
                svg {
                    margin-right: 8px;
                    @include max576 {
                        margin-right: 4px;
                    }
                }
                span {
                    font-weight: 600;
                    color: #1B2124; 
                }
            }
        }
        li {
            padding: 0px 20px;
            height: 70px;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include max768 {
                border: 1.5px solid #F3F3F3;
                padding: 0px 16px;
            }
            &:not(:last-child) {
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    width: 1px;
                    height: 32px;
                    transform: translateY(-50%);
                    background: $lightgrey1;
                    right: 0;
                    @include max768 {
                        display: none;
                    }
                }
            }
        }
    }
    .description {
        margin-bottom: 40px;
        .h2 {
            margin-bottom: 12px;
            @include max576 {
                font-style: 14px;
            }
        }
        .text {
            font-weight: 400;
            font-size: 14px;
            line-height: 160%;
            color: #7F879E; 
            margin-bottom: 12px;
        }
        .link {
            cursor: pointer;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #1F8FFF;
            display: flex;
            align-items: center;
            justify-content: start;
            span {
                margin-right: 4px;
            }
        }
    }
    .applications-grid {
        &__inner {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px;
            @include max768 {
                grid-template-columns: 1fr
            }
        }
        .devs-item {
            @include devCard;
            background: #FFFFFF;
            box-shadow: 0px 4px 20px rgba(127, 135, 158, 0.15);
            border-radius: 12px;
            @include max576 {
                .pm {
                    font-size: 12px;
                }
                &__info {
                    ul {
                        margin-bottom: 16px;
                    }
                }
                .btn {
                    span span {
                        display: none;
                    }
                }
            }
        }
        .row {
            display: flex;
            align-items: center;
            margin-bottom: 18px;
            .p3 {
                margin-left: auto;
                color: #7F879E;
                margin-right: 12px;
                @include max576 {
                    display: none;
                }
            }
            .select-wrapper {
                @include customSelect;
                width: 144px;
                height: 44px;
                flex: 0 0 144px;
                background: #F3F3F3;
                @include max576 {
                    margin-left: auto;
                    width: 120px;
                    height: 32px;
                    flex: 0 0 120px;
                    font-size: 12px;
                }
            }
        }
    }
}

.editapp-popup {
    max-width: 800px;
    width: calc(100% - 40px);
    background: #FFFFFF;
    border-radius: 12px;
    overflow: hidden;
    position: relative;
    padding: 34px 30px 47px;
    @include max576 {
        padding: 24px 20px 34px;
    }
    .h1 {
        margin-bottom: 50px;
        position: relative;
        @include max576 {
            margin-bottom: 45px;
            &::after {
                content: "";
                position: absolute;
                bottom: -24px;
                width: calc(100% + 40px);
                left: -20px;
                height: 1.5px;
                background: #F3F3F3;
            }
        }
    }
    form {
        &>*:not(:last-child) {
            margin-bottom: 40px;
            @include max992 {
                margin-bottom: 30px;
            }
            @include max768 {
                margin-bottom: 22px;
            }
        }
    }
    position: relative;
    .editapp-close {
        @include closePopup;
    }
    .input-group {
        @include inputGroup;
        .input-wrapper {
            input {
                height: 54px;
                &:not(:placeholder-shown) {
                    border: 2px solid transparent;
                }
                @include max576 {
                    height: 46px;
                }
            }
            .badge {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 12px;
                @include badge(#268D61);
                @include max576 {
                    display: none;
                }
            }
            &.no-icon {
                input {
                    padding-left: 12px;
                }
            }
        }
        .select-wrapper {
            position: relative;
            svg {
                &:last-child {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 18px;
                }
            }
            &.has-icon {
                select {
                    padding-left: 57px;
                    @include max576 {
                        padding-left: 47px;
                    }
                }
            }
    
            .select-icon {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 20px;
                @include max576 {
                    left: 11px;
                }
            }
            select {
                width: 100%;
                padding: 0 29px;
                border: none;
                height: 54px;
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                outline: none;
                background: #F6F8F9;
                border-radius: 12px;
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
                color: #1B2124;
                @include max1200 {
                    height: 50px;
                }

                @include max576 {
                    height: 46px;
                }
                option {
                    &[disabled] {
                        color: #AAAFBE;
                    }
                }

                &::-ms-expand {
                    display: none;
                }
            }
        }
    }
    .input-row {
        display: grid;
        grid-gap: 23px;
        grid-template-columns: 1fr 1fr;
        @include max768 {
            &.nowrap {
                grid-gap: 15px;
            }
            &:not(.nowrap) {
                grid-template-columns: 1fr;
                grid-gap: 22px;
            }
        }
        .input-group {
            margin-bottom: 0;
        }
    }
    .radio-group {
        display: flex;
        align-items: center;
        .radio-wrapper {
            display: flex;
            align-items: center;
            &:first-child {
                margin-right: 40px;
                @include max576 {
                    margin-right: 26px;
                }
            }
        }
        .radio-item {
            @include radioItem;
        }
    }
    .half-wrapper {
        max-width: 342px; 
        @include max768 {
            max-width: 100%;
        }
    }
    .tags-group {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-top: -6px;
        .tag {
            margin-top: 6px;
            background: #F9F9F9;
            border-radius: 12px;
            padding: 8px 14px;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            margin-right: 9px;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            height: 32px;
            transition: all .3s ease;
            svg {
                margin-left: 8px;
                cursor: pointer;
            }
            &:hover {
                color: #3D425A;
                svg path {
                    stroke: #3D425A;
                }
            }
        }
        .add-btn {
            border: 1px solid #1F8FFF;
            border-radius: 12px;  
            width: 32px;
            height: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            margin-top: 6px;
            .mobile-version {
                display: none;
            }
            @include max576 {
                border: none;
                svg {
                    display: none;
                    &.mobile-version {
                        display: block;
                    }
                }
            }
        }
    }
    .textarea-wrapper {
        textarea {
            background: #F6F8F9;
            border-radius: 12px;
            height: 150px;
            width: 100%;
            resize: none;
            padding: 12px 20px 12px;
			border: 2px solid transparent;
            outline: none;
			&:hover {
				background: $lightgrey1;
			}
			&:focus {
				background: $lightgrey3;
				border: 2px solid $green3;
			}
			&:active {
				background: white;
				border: 2px solid $grey3;
			}
            @include max576 {
                padding: 12px 13px 12px;
            }
        }
    }
    .btn-group {
        display: grid;
        grid-template-columns: 128px 128px;
        grid-gap: 12px;
        justify-content: end;
        @include max576 {
            grid-gap: 16px;
            grid-template-columns: 1fr;
            justify-content: unset;
            .secondary {
                grid-row: 1 / 2;
            }
            .neutral {
                grid-row: 2 / 3;
            }
        }
        .btn {
            height: 44px;
            font-size: 12px;
            line-height: 130%;
        }
    }
    .mobile-group {
        display: none;
        @include max576 {
            display: block;
        }
        .select-wrapper {
            select {
                padding: 0 12px;
            }
        }
    }
}

