                @include checkbox;
            }
        }
        .test-timer {
            grid-column: 2 / 3;
            background: #FFFFFF;
            border-radius: 12px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            padding: 36px 0 46px;
            height: fit-content;
            @include max768 {
                grid-column: unset;
            }
            @include max576 {
                padding: 10px 0;
            }
