                font-weight: 300;
                font-size: 64px;
                line-height: 150%;
                color: #47D18C;
                margin-top: 7px;
                @include max992 {
                    font-size: 50px;
                    line-height: 130%;
                    margin-top: 0;
                }
                @include max576 {
                    font-size: 36px;
                    line-height: 122%;
                }
            }
            .h2 {
                font-size: 14px;
            }
        }
        .test-nav {
            grid-column: 1 / 2;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include max768 {
                grid-column: unset;
            }
            .btn-row {
                display: grid;
                grid-template-columns: 128px 128px;
                grid-gap: 12px;
                @include max576 {
                    grid-template-columns: 48px 48px;
                    grid-gap: 8px;
