                }
                &.btn-next {
                    svg {
                        margin-left: 8px;
                        @include max576 {
                            margin: 0;
                        }
                    }
                }
            }
            .btn-exit {
                width: 128px;
                flex: 0 0 128px;
                color: #7F879E;
            }
        }
    }

