        font-size: 22px;
        line-height: 130%;
        color: #1B2124;
        margin-bottom: 6px;
        @include max768 {
            font-size: 20px;
        }
        @include max576 {
            font-size: 16px;
        }
    }
    ul.info {
        margin-top: -6px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        li {
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #7F879E;
            margin-top: 6px;
            display: flex;
            align-items: center;
            p {
                margin-left: 8px;
                span:first-child {
                    font-weight: 600;
                    color: #1B2124;
                }
            }
            svg {
                &.info-alt {
                    cursor: pointer;
                    margin-left: 8px;
                }
            }
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }
    ul.requirement {
        border: 1.5px solid #F3F3F3;
        border-radius: 12px;  
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        margin: 24px 0;
        font-weight: 400;
        font-size: 12px;
        line-height: 160%;
        overflow: hidden;
        color: #7F879E;
        @include max768 {
            grid-template-columns: 1fr 1fr;
        }
        @include max576 {
            grid-template-columns: 54% 46%;
        }
        p {
            &:not(:last-child) {
                margin-bottom: 5px;
            }
            &:last-child {
                display: flex;
                align-items: center;
                svg {
                    margin-right: 8px;
                    @include max576 {
                        margin-right: 4px;
                    }
                }
                span {
                    font-weight: 600;
                    color: #1B2124; 
                }
            }
        }
        li {
            padding: 0px 20px;
            height: 70px;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include max768 {
                border: 1.5px solid #F3F3F3;
                padding: 0px 16px;
            }
            &:not(:last-child) {
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    width: 1px;
                    height: 32px;
                    transform: translateY(-50%);
                    background: $lightgrey1;
                    right: 0;
                    @include max768 {
                        display: none;
                    }
                }
            }
        }
    }
    .description {
        margin-bottom: 40px;
        .h2 {
            margin-bottom: 12px;
            @include max576 {
                font-style: 14px;
            }
        }
        .text {
            font-weight: 400;
            font-size: 14px;
            line-height: 160%;
            color: #7F879E; 
            margin-bottom: 12px;
        }
        .link {
            cursor: pointer;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #1F8FFF;
            display: flex;
            align-items: center;
            justify-content: start;
            span {
                margin-right: 4px;
            }
        }
    }
    .links {
        .h2 {
            margin-bottom: 16px;
        }
    }
    ul.list {
        li {
            &:not(:last-child) {
                margin-bottom: 12px;
            }

            a {
                display: flex;
                align-items: center;
                color: #7F879E;

                svg {
                    margin-right: 8px;
                }
            }
        }
    }
    .row__bottom {
        border-top: 2px solid #F3F3F3;
        margin-top: 30px;
        padding: 22px 0 0;
        .btn {
            height: 38px;
            font-size: 12px;
            @include max576 {
                height: 44px;
            }
            svg {
                margin-right: 8px;
            }
        }
    }
    .btn-row {
        display: grid;
        grid-template-columns: 160px 160px;
        align-items: center;    
        justify-content: end;
        grid-gap: 20px;
        @include max576 {
            grid-template-columns: 1fr 1fr;
            grid-gap: 12px;
        }
    }
}
.layer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: none;
    &.active {
        display: block;
    }
}
.p-close {
    transition: all .3s ease;
    path {
        transition: all .3s ease;
        stroke: #7F879E;
    }
    &:hover {
        border-color: #AAAFBE;
        path {
            stroke: #252A3F;
        }

    }
}