.dashboard-wrapper {
    .sidebar {
        background: #FFFFFF;
        border-right: 1px solid #F3F3F3;
        padding: 40px 16px 54px;
        display: flex;
        flex-direction: column;
        position: sticky;
        top: 0;
        left: 0;
        height: 100vh;
        &.no-desk {
            display: none;
            @include max992 {
                display: flex;
            }
        }
        .row {
            display: flex;
            align-items: center;
            margin-bottom: 49px;
    
            @include max992 {
                margin-bottom: 70px;
            }
        }
    
        .burger {
            display: none;
            margin-right: 0px;
    
            @include max992 {
                display: block;
                margin-left: 4px;
            }
        }
    
        @include max992 {
            position: fixed;
            top: 0;
            left: 0;
            transform: translateX(-100%);
            width: 260px;
            transition: all .3s ease;
            z-index: 10;
            height: 100%;
            padding-top: 28px;
            overflow-x: hidden;
            &.active {
                transform: translateX(0);
            }
        }
    
        .logo {
            margin-left: 24px;
    
            @include max992 {
                margin-bottom: 8px;
                margin-left: 14px;
            }
    
            @include max576 {
                margin-bottom: 4px;
    
                img {
                    width: 98px;
                    height: auto;
                }
            }
        }
    
        &__inner {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: #7F879E;
    
            .title {
                height: 48px;
                padding: 0 20px;
                display: flex;
                align-items: center;
                justify-content: start;
                border-radius: 10px;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                background: #47D18C;
                color: #FFFFFF;
                margin-bottom: 16px;
    
                svg {
                    margin-right: 14px;
                }
            }
    
            &>ul {
                &>li {
                    &:not(:last-child) {
                        margin-bottom: 15px;
                    }
                    &:last-child {
                        margin-bottom: 30px;
                    }
    
                    &.mb-item {
                        margin-bottom: 50px;
                    }
    
                    .sidebar-item {
                        cursor: pointer;
                        height: 48px;
                        display: flex;
                        align-items: center;
                        padding: 0 20px;
    
                        &.active {
                            border-radius: 10px;
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 130%;
                            background: #47D18C;
                            color: white;
                            .stroke path {
                                stroke: white;
                            }
    
                            .fill path {
                                fill: white;
                            }
                            
                            svg:last-child path {
                                stroke: white;
                            }
    
                            .mixed path {
                                stroke: white;
                            }
                            path {
                                &.has-message {
                                    fill: #F05817;
                                    stroke: #F05817;
                                }
                            }
                        }
    
                        svg:first-child {
                            margin-right: 12px;
                        }
    
                        svg:last-child {
                            margin-left: auto;
                        }
    
                        .active-value {
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 130%;
                            color: #47D18C;
                            margin-left: auto;
                            @include max576 {
                                padding: 0;
                            }
                        }
                    }
                }
            }
        }
    
        &-subitems {
            padding: 12px 0;
            display: none;
    
            li {
                padding-left: 56px;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
    
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
    
                &.active {
                    color: #1F8FFF;
                }
            }
        }
    
        .logout-btn {
            height: 48px;
            display: flex;
            align-items: center;
            padding: 0 20px;
            margin-top: auto;
            color: #F05817;
    
            svg {
                margin-right: 12px;
            }
        }
    }
}