.dashboard-wrapper {
    padding-right: 50px;
    display: grid;
    grid-template-columns: minmax(260px, 1fr) 5.5fr;
    grid-gap: 24px;
    background: linear-gradient(198.65deg, rgba(193, 227, 211, 0.52) 12.62%, rgba(245, 248, 252, 0.45) 41.35%), #FBF3E5;
    min-height: 100vh;
    &.fluid {
        display: block;
        padding-right: 0;
        padding-left: 0;
        .container {
            max-width: 1144px;
            width: 100%;
            padding: 0 20px;
            margin: 0 auto;
        }
    }

    @include max1200 {
        grid-gap: 15px;
        padding-right: 20px;
    }

    @include max992 {
        grid-template-columns: 1fr;
        padding-left: 20px;
    }

    @include max576 {
        background: linear-gradient(198.65deg, rgba(193, 227, 211, 0.52) 12.62%, rgba(245, 248, 252, 0.45) 41.35%), #FBF3E5;
        background: linear-gradient(179.34deg, rgba(193, 227, 211, 0.52) 0.57%, rgba(245, 248, 252, 0.45) 19.85%), #FBF3E5;
    }



    .content {
        &__inner {
            display: grid;
            grid-gap: 24px;
            grid-template-columns: 3fr minmax(285px, 1.25fr);
            padding-bottom: 20px;
            &.company-team {
                display: block;
            }
            @include max1200 {
                grid-gap: 15px;
            }
            &.company-content {
                padding-bottom: 40px;
            }
            @include max768 {
                grid-template-columns: 1fr;

                &.company-content {
                    .content__inner-side {
                        grid-row: 1 / 2;
                    }

                    .content__inner-base {
                        grid-row: 2 / 3;
                    }
                }
            }

            &-base>* {
                &:not(:last-child) {
                    margin-bottom: 40px;

                    @include max1200 {
                        margin-bottom: 20px;
                    }
                }
            }

            &-base>* {
                &.simple-block {
                    &:not(:last-child) {
                        margin-bottom: 18px;

                        @include max768 {
                            margin-bottom: 13px;
                        }
                    }
                }
            }

            &.content-fluid {
                grid-template-columns: minmax(330px, 424px) 1.6fr;
                grid-gap: 0;
                @include max1200 {
                    grid-template-columns: minmax(260px, 0.8fr) 1.6fr;
                }
                @include max768 {
                    display: block;
                }
            }
            &.content-fluid-reverse {
                grid-template-columns: 1.6fr minmax(330px, 1fr);
                @include max768 {
                    display: block;
                }
            }
            &.content-fluid-full {
                display: block;
                .interview-calendar {
                    // img {
                    //     width: 100%;
                    // }
                }
                @include max992 {
                    display: none;
                }
            }
        }
        &.message-content {
            padding-bottom: 0;
        }
        &__top {
            @include max576 {
                height: 130px;
                position: relative;
                overflow-y: hidden;
                margin-right: -20px;
                width: calc(100% + 20px);
            }

            &-wrapper {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                grid-gap: 24px;
                color: #7F879E;

                @include max1200 {
                    grid-gap: 15px;
                }

                @include max576 {
                    position: absolute;
                    top: 0;
                    left: 0;
                    grid-template-columns: repeat(3, 224px);
                    grid-gap: 12px;
                    height: 100%;
                }
            }

            &-item {
                padding: 24px 22px;
                background: #FFFFFF;
                border-radius: 12px;

                @include max576 {
                    width: 224px;
                    height: 100%;
                }
            }

            .row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 20px;
            }

            h3 {
                color: $setBlack1;
            }
        }

        &__chart {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 33px 24px;

            @include max576 {
                padding: 23px 15px;
                margin-bottom: 36px !important;
            }

            img {
                width: 100%;
            }

            ul {
                display: flex;
                align-items: center;
                margin-right: 45px;

                @include max1200 {
                    margin-right: 20px;
                }

                @include max576 {
                    display: none;
                }

                li {
                    display: flex;
                    align-items: center;

                    &:first-child {
                        margin-right: 16px;

                        span {
                            background: #1F8FFF;
                        }
                    }

                    &:last-child {
                        span {
                            background: #78C58C;
                        }
                    }

                    span {
                        display: block;
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        margin-right: 8px;
                    }
                }
            }

            &>ul {
                display: none;

                @include max576 {
                    display: flex;
                    margin: 15px 0;
                }
            }
        }

        .title-row {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .chart-info {
                display: flex;
                align-items: center;
            }

            .chart-select {
                height: 32px;
                border: 1.5px solid #F3F3F3;
                border-radius: 8px;
                width: 73px;
                flex: 0 0 73px;
                position: relative;

                select {
                    padding: 0 12px;
                    @include selectDefault;
                    border: none;
                    background: transparent;
                    height: 100%;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 130%;
                }

                svg {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 12px;
                }
            }
        }

        &__recommend {
            .view-link {
                display: flex;
                align-items: center;
                color: #1F8FFF;
                font-weight: 600;
                font-size: 14px;
                line-height: 22px;

                @include max576 {
                    font-size: 12px;
                }

                svg {
                    margin-left: 4px;

                    @include max576 {
                        width: 18px;
                        height: 18px;
                    }
                }
            }

            .title-row {
                margin-bottom: 24px;

                @include max1200 {
                    margin-bottom: 16px;
                }
            }
        }

        .devs-grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 20px 15px;

            @include max1400 {
                grid-template-columns: 1fr;
            }

            @include max576 {
                grid-gap: 12px;
            }
        }

        .devs-item {
            @include devCard;
        }

        &__inner-side {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 24px;
            height: fit-content;

            @include max768 {
                display: none;

                &.mobile__view {
                    display: block;
                    background: unset;
                    padding: 0;
                }
            }

            .side-block {
                @include sideBlock;
            }

            .circle {
                margin: 0 auto 6px;
                --v: calc(((18/5) * var(--p) - 90)*1deg);
                width: 109px;
                height: 109px;
                display: inline-block;
                border-radius: 50%;
                padding: 2px;
                position: relative;
                background:
                    linear-gradient(#FFFFFF, #FFFFFF) content-box,
                    linear-gradient(var(--v), $grey3 50%, transparent 0) 0/min(100%, (50 - var(--p))*100%),
                    linear-gradient(var(--v), transparent 50%, $blue1 0) 0/min(100%, (var(--p) - 50)*100%),
                    linear-gradient(to right, $grey3 50%, $blue1 0);

                .img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 80%;
                    height: 80%;
                    background: #F3F3F3;
                    padding: 5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 50%;
                    overflow: hidden;

                    &::after {
                        content: "";
                        width: 100%;
                        transition: all .3s ease;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        background: rgba(27, 33, 36, 0.5);
                        z-index: 2;
                        opacity: 0;
                    }
                }

                svg {
                    opacity: 0;
                    pointer-events: none;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 3;
                    cursor: pointer;
                    transition: all .3s ease;
                }

                &:hover {
                    .img {
                        &::after {
                            opacity: 1;
                        }

                        svg {
                            opacity: 1;
                            pointer-events: visible;
                        }
                    }
                }
            }

            .simple-block__side {
                @include max768 {
                    padding: 20px 20px 30px;
                    background: white;
                    border-radius: 12px;

                    &:not(:last-child) {
                        margin-bottom: 13px;
                    }

                    .h1 {
                        font-size: 18px;
                    }
                }

                &:not(:last-child) {
                    .side-block {
                        &:last-child {
                            border-bottom: 1px solid #f3f3f3;
                            margin-bottom: 22px;

                            @include max768 {
                                border-bottom: none;
                                margin-bottom: 0;
                            }
                        }
                    }
                }

                .side-block {
                    @include max768 {
                        padding-bottom: 20px;

                        &:last-child {
                            padding-bottom: 0;
                        }
                    }

                    .row-grid {
                        display: grid;
                        grid-template-columns: auto 174px;
                        grid-gap: 10px;
                        margin: 25px 0;
                    }

                    .btn {
                        margin-left: auto;
                        width: 174px;
                        height: 44px;
                        font-size: 12px;
                    }

                    .team-list {
                        margin: 18px 0;

                        @include max576 {
                            display: grid;
                            grid-template-columns: repeat(6, 40px);
                            grid-gap: 10px;
                        }

                        &__item {
                            @include teamMinItem;
                        }
                    }

                    .link-all {
                        display: flex;
                        align-items: center;
                        justify-content: end;
                        color: #1F8FFF;

                        span {
                            margin-right: 6px;
                        }
                    }
                }
            }


        }

        &__row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 33px;
            .payment-details__mobile {
                width: 42px;
                height: 42px;
                display: none;
                align-items: center;    
                justify-content: center;
                background: #FFFFFF;
                border-radius: 12px;
                margin-left: auto;
                cursor: pointer;
                @include max768 {
                    display: flex;
                }
            }
            &.mobile-row {
                .btn {
                    font-size: 12px;
                    padding: 0 14px;
                }
            }
            &.desk-row {
                @include max768 {
                    .content__row-action {
                        display: none;
                    }
                }
            }
            @include max768 {
                display: grid;
                grid-template-columns: 1fr;
                grid-gap: 20px;
                &.mobile-row {
                    grid-template-columns: 40% 1fr;
                    .btn {
                        width: 100%;
                        margin-left: auto;
                    }
                }
            }

            @include max576 {
                margin-bottom: 20px;
            }

            .select-wrapper {
                @include customSelect;
                height: 44px;
                width: 144px;
                flex: 0 0 144px;
                font-weight: 600;

                @include max576 {
                    height: 42px;
                    font-size: 12px;
                }
            }

            p {
                color: #7F879E;
            }

            &-action {
                display: flex;
                align-items: center;

                &>*:not(:last-child) {
                    margin-right: 16px;
                    @include max576 {
                        margin-right: 0;
                    }
                }

                .p3 {
                    @include max576 {
                        display: none;
                    }
                }

                @include max576 {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-gap: 20px;

                    .select-wrapper {
                        flex: unset;
                        width: unset;
                        margin-right: 0;
                    }
                    .white {
                        color: #1B2124;
                    }
                }
            }

            &-title {
                display: flex;
                justify-content: space-between;

                .mobile-sort {
                    display: none;
                    width: 42px;
                    height: 42px;
                    justify-content: center;
                    align-items: center;
                    background: #FFFFFF;
                    border-radius: 12px;
                    cursor: pointer;

                    @include max768 {
                        display: flex;
                    }
                }

                h5 {
                    margin-bottom: 4px;
                }
            }
            .select-wrapper {
                @include max576 {
                    max-width: 100%;
                    width: 100%;
                }
            }
            .btn {
                height: 46px;
                padding: 0 24px;
                font-size: 14px;
                line-height: 130%;
                max-width: 170px;
                @include max576 {
                    max-width: 100%;
                }
                svg {
                    margin-left: 8px;
                }
                &.resume-btn,
                &.preview-js {
                    svg {
                        margin-left: 0;
                        margin-right: 9px;
                    }
                }
                @include max576 {
                    height: 42px;
                    font-size: 12px;
                    padding: 0 14px;
                }
            }
        }

        .vacancy-grid,
        .timesheet-grid {
            display: grid;
            grid-template-columns: 1fr;
            grid-gap: 28px;

            @include max576 {
                grid-gap: 12px;
            }
        }

        .vacancy-item {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 20px 20px 36px;
            border: 2px solid transparent;
            cursor: pointer;
            transition: all .3s ease;
            &:hover {
                border-color: #B1DFC9;
            }

            &__top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 24px;

                .badge {
                    line-height: 127%;
                }
            }

            &__action {
                @include elementAction;
            }

            &__inner {
                &>*:not(:last-child) {
                    margin-bottom: 12px;
                }

                ul {
                    margin-top: -6px;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    li {
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 130%;
                        color: #7F879E;
                        margin-top: 6px;
                        display: flex;
                        align-items: center;

                        p {
                            margin-left: 8px;

                            span:first-child {
                                font-weight: 600;
                                color: #1B2124;
                            }
                        }

                        .tooltip {
                        margin-left: 8px;
                        }

                        &:not(:last-child) {
                            margin-right: 20px;
                        }
                    }
                }

                .text {
                    color: #7F879E;

                    @include max576 {
                        font-size: 12px;
                    }
                }
            }

            &__bottom {
                margin-top: 36px;
                display: flex;
                align-items: end;
                justify-content: space-between;

                @include max576 {
                    margin-top: 22px;
                    display: grid;
                    grid-template-columns: 1fr;
                    grid-gap: 20px;
                }

                .btn {
                    width: 156px;
                    height: 44px;
                    font-size: 12px;
                    line-height: 130%;

                    svg {
                        margin-right: 4px;
                    }
                }

                .date {
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: right;
                    color: #7F879E;

                    span {
                        color: $setBlack1
                    }

                    @include max576 {
                        text-align: left;
                    }
                }

                .h3 {
                    margin-bottom: 12px;
                }

                ul {
                    display: flex;
                    align-items: center;

                    li {
                        border: 1.5px solid #FFFFFF;
                        overflow: hidden;
                        border-radius: 50%;
                        width: 38px;
                        height: 38px;
                        justify-content: center;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        background: #F3F3F3;
                        font-weight: 400;
                        font-size: 12px;
                        line-height: 130%;
                        color: #1B2124;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }

                        &:not(:first-child) {
                            margin-left: -8px;
                        }
                    }
                }
            }
        }

        .timesheet-item {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 20px 20px 36px;
            border: 2px solid transparent;
            cursor: pointer;
            transition: all .3s ease;
            &:hover {
                border-color: #B1DFC9;
            }
            @include max576 {
                padding: 20px 20px 31px;
            }

            &__top {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 24px;

                .badge {
                    line-height: 127%;
                    width: 108px;

                    @include max576 {
                        height: 28px;
                    }
                }

                .date {
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: right;
                    color: #7F879E;

                    span {
                        color: $setBlack1
                    }

                    @include max576 {
                        text-align: left;
                    }
                }
            }

            &__inner {
                &>*:not(:last-child) {
                    margin-bottom: 12px;
                }

                .h1 {
                    color: #1F8FFF;
                }

                ul {
                    margin-top: -6px;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    li {
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 130%;
                        color: #7F879E;
                        margin-top: 6px;
                        display: flex;
                        align-items: center;

                        p {
                            margin-left: 8px;

                            span:last-child {
                                font-weight: 600;
                                color: #1B2124;
                            }
                        }

                        .tooltip {
                        margin-left: 8px;
                        }

                        &:not(:last-child) {
                            margin-right: 20px;
                        }
                    }
                }

                .text {
                    margin-top: 20px;
                    color: #7F879E;

                    @include max576 {
                        font-size: 12px;
                    }
                }
            }

            &__bottom {
                .dev-row {
                    &>.p4 {
                        @include max576 {
                            display: none;
                        }
                    }

                    .mobp {
                        display: none;
                        font-weight: 600;
                        font-size: 12px;
                        line-height: 130%;
                        color: #1B2124;
                        margin-left: auto;

                        @include max576 {
                            display: block;
                        }
                    }

                    margin-top: 20px;
                    display: flex;
                    align-items: end;

                    @include max576 {
                        align-items: center;
                    }

                    .img {
                        width: 42px;
                        height: 42px;
                        flex: 0 0 42px;
                        margin-right: 12px;
                        overflow: hidden;
                        border-radius: 50%;

                        @include max576 {
                            width: 34px;
                            height: 34px;
                            flex: 0 0 34px;
                        }
                    }

                    .content {
                        margin-right: 22px;
                    }

                    .h2 {
                        @include max576 {
                            font-size: 12px;
                            line-height: 100%;
                            margin-bottom: 4px;
                        }
                    }

                    p {
                        color: #7F879E;

                        span {
                            font-weight: 600;
                            color: #1B2124;
                        }

                        @include max576 {
                            font-size: 11px;
                            line-height: 100%;
                        }
                    }
                }
            }
        }

        .filter {
            @include filter;
        }

        .simple-block {
            background: #FFFFFF;
            border-radius: 12px;
            padding: 28px 24px 36px;
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;

            @include max576 {
                font-size: 14px;
                line-height: 130%;
            }

            &__inner {
                position: relative;
                &>.edit {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 28px;
                    height: 28px;
                    border: 1px solid #F3F3F3;
                    border-radius: 50%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &:hover {
                        border: 1px solid #D4D7DF;
    
                        path {
                            stroke: #3D425A;
                        }
                    }
                }
                .h1 {
                    margin-bottom: 28px;

                    @include max576 {
                        margin-bottom: 22px;
                    }
                }

                table {
                    width: 100%;
                    border-collapse: collapse;

                    td {
                        padding-bottom: 20px;

                        @include max576 {
                            width: 50%;
                        }
                        .row {
                            display: flex;
                            align-items: center;
                            svg {
                                margin-left: 6px;
                                cursor: pointer;
                            }
                        }
                        &:first-child {
                            vertical-align: top;
                            color: #AAAFBE;
                            width: 140px;

                            @include max576 {
                                width: 50%;
                            }
                        }

                        ul {
                            li {
                                &:not(:last-child) {
                                    margin-bottom: 12px;
                                }

                                a {
                                    display: flex;
                                    align-items: center;
                                    color: #7F879E;

                                    svg {
                                        margin-right: 8px;
                                    }
                                }
                            }
                        }
                    }
                }

                .text {
                    color: #7F879E;
                    margin-bottom: 24px;
                }

                .list {
                    p {
                        display: flex;
                        align-items: center;
                        color: #7F879E;

                        &:not(:last-child) {
                            margin-bottom: 12px;
                        }

                        svg {
                            margin-right: 5px;
                        }
                    }
                }
                .upload-container {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-gap: 24px;
                    margin-bottom: 24px;
                    align-items: center;
                    @include max1200 {
                        grid-template-columns: 1fr;
                    }
                }
                .upload-zone {
                    background: #F8F8F8;
                    border: 1.5px dashed #47D18C;
                    border-radius: 12px;
                    height: 54px;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #7F879E;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    span {
                        color: #47D18C;
                    }
                }
                .upload-item {
                    background: #F6F8F9;
                    border-radius: 12px;
                    height: 74px;
                    padding: 0 12px;
                    display: grid;
                    grid-template-columns: 48px 1fr 48px;
                    align-items: center;
                    grid-gap: 12px;
                    &__icon {
                        background: #FFFFFF;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 48px;
                        height: 48px;
                        border-radius: 50%;
                    }
                    .h4 {
                        font-weight: 600;
                        margin-bottom: 4px;
                    }
                    &__remove {
                        cursor: pointer;
                    }
                    
                }
                .video-container {
                    &__inner {
                        position: relative;
                        .play {
                            cursor: pointer;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%,-50%);
                            width: 76px;
                            height: 76px;
                            display: flex;  
                            align-items: center;
                            justify-content: center;
                            background: rgba(255, 255, 255, 0.27);
                            border-radius: 50%;
                            @include max768 {
                                width: 60px;
                                height: 60px;
                            }
                        }
                        video {
                            border-radius: 11.1318px;
                            width: 100%;
                            height: 337px;
                            object-fit: cover;
                            @include max768 {
                                height: 200px;
                            }
                            @include max576 {
                                height: 161px;
                            }
                        }
