                                border-radius: 8px;
                            }
                            .radio-wrapper {
                                display: flex;
                                align-items: center;
                            }
                            .radio-item {
                                @include radioItem;
                                pointer-events: none;
                            }
                        }
                    }
                }
            }
            &-row {
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 44px;
                .title {
                    font-weight: 600;
                    font-size: 20px;
                }
                .btn {
                    height: 44px;
                    font-weight: 600;
                    font-size: 12px;
                    line-height: 130%;
                    width: 150px;
                    svg {
                        margin-right: 6px;
                    }
                }
                .select-wrapper {
                    @include customSelect;
                    height: 44px;
                    width: 144px;
                    flex: 0 0 144px;
                    font-weight: 600;
    
                    @include max576 {
                        height: 42px;
                        font-size: 12px;
                    }
                }
            }
            &-head {
                &:not(:last-child) {
                    margin-bottom: 36px;
                    @include max576 {
                        margin-bottom: 24px;
                    }
                }
                .back-table {
                    font-size: 16px;
                    line-height: 130%;
                    color: #1B2124;
                    display: flex;  
                    align-items: center;
                    margin-bottom: 34px;
                    svg {
                        margin-right: 15px;
                    }
                }
                .details-grid {
                    display: grid;
                    grid-template-columns: 1fr 1fr;
                    grid-gap: 17px 24px;
                    @include max768 {
                        grid-template-columns: 1fr;
                        grid-gap: 12px;
                    }
                    &>* {
                        background: #FFFFFF;
                        border-radius: 12px;
                        padding: 18px 24px 36px;
                    }
                    .h3 {
                        margin-bottom: 30px;
                        @include max576 {
                            margin-bottom: 24px;
                        }
                    }
                    .team-item {
                        @include teamMinItem;
                        .img {
                            flex: 0 0 56px;
                            width: 56px;
                            height: 56px;
                            margin-right: 12px;
                            img {
                                width: 100%;
                            }
                        }
                        .content {
                            flex: 1;
                            @include max576 {
                                display: block;
                            }
                        }
                        ul {
                            display: flex;
                            align-items: center;
                            @include max576 {
                                display: grid;
                                grid-template-columns: 1fr;
                                grid-gap: 5px;
                            }
                            li {
                                display: flex;
                                align-items: center;
                                position: relative;
                                color: #7F879E;
                                &:not(:last-child) {
                                    margin-right: 26px;
                                    &::after {
                                        content: "";
                                        width: 2px;
                                        height: 2px;
                                        border-radius: 50%;
                                        position: absolute;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        right: -13px;
                                        background: #7F879E;
                                        @include max576 {
                                            display: none;
                                        }
                                    }
                                }
                                svg {
                                    margin-right: 10px;
                                }
                            }
                        }
                    }
                    &__top {
                        grid-column: 1 / 3;
                        @include max768 {
                            grid-column: unset;
                        }
                    }
                    &__result {
                        &-inner {
                            display: grid;
                            grid-template-columns: 135px 1fr;
                            grid-gap: 10px;
                            .h2 {
                                color: #47D18C;
                                margin-bottom: 12px;
                            }
                            p {
                                color: #7F879E; 
                            }
                        }
                        
                    .pie {
                        position: relative;
                        text-align: center;
                        display: flex;
                        align-items: center;
                        justify-content: start;
                        width: fit-content;
                        img {
                            width: 135px;
                        }
                        .info {
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%,-50%);
                            .percent {
                                font-weight: 300;
                                font-size: 24px;
                                line-height: 150%;
                                color: #252A3F;
                                @include max576 {
                                    font-size: 24px;
                                    margin-bottom: 0;
                                }
                            }
                            p {
                                font-size: 13.4521px;
                                line-height: 160%;
                                color: #7F879E;
                                white-space: nowrap;
                                @include max576 {
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                    }
                    &__timer {
                        &-inner {
                            .h1 {
                                margin-bottom: 12px;
                            }
                            &>p {
                                color: #7F879E;
                                span {
                                    color: #1B2124;
                                }
                            }
                        }
                        .progress {
                            background: #F3F3F3;
                            border-radius: 10px;
                            height: 6px;
                            margin-top: 24px;
                            margin-bottom: 16px;
                            display: flex;
                            span {
                                background: #47D18C;
                                border-radius: 10px;
                                height: 100%;
                                display: flex;
                            }
                        }
                        .row {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            .p4 {
                                color: #7F879E;
                            }
                        }
                    }
                }
            }
        }
        &__fluid {
            display: block;
            .search-page {
                padding: 32px 26px 100px;
                background: #FFFFFF;
                border-radius: 12px;
                @include max576 {
                    background: unset;
                    padding: 0;
                }
                .team-item {
                    @include teamMinItem;
                    &:not(:last-child) {
                        margin-bottom: 92px;
                        @include max768 {
                            margin-bottom: 30px;
                        }
                    }
                    .img {
                        flex: 0 0 64px;
                        width: 64px;
                        height: 64px;
                        margin-right: 20px;
                        @include max576 {
                            margin-right: 16px;
                        }
                        img {
                            width: 100%;
                        }
                    
                        &.logo-img {
                            background: #F3F3F3;
                            display: flex;
                            align-items: center;    
                            justify-content: center;
                            img {
                                width: unset;
                                height: unset;
                            }
                        }
                    }
                    .content {
                        flex: 1;
                        @include max576 {
                            display: block;
                        }
                    }
                    ul {
                        display: flex;
                        align-items: center;
                        @include max576 {
                            display: grid;
                            grid-template-columns: 1fr;
                            grid-gap: 8px;
                        }
                        li {
                            display: flex;
                            align-items: center;
                            position: relative;
                            color: #7F879E;
                            @include max576 {
                                font-size: 12px;
                            }
                            &:not(:last-child) {
                                margin-right: 26px;
                                &::after {
                                    content: "";
                                    width: 2px;
                                    height: 2px;
                                    border-radius: 50%;
                                    position: absolute;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    right: -13px;
                                    background: #7F879E;
                                    @include max576 {
                                        display: none;
                                    }
                                }
                            }
                            svg {
                                margin-right: 10px;
                            }
                        }
                    }
                }
                &__inner {
                    display: grid;
                    grid-template-columns: 2fr 1fr;
                    grid-gap: 30px;
                    @include max768 {
                        grid-template-columns: 1fr;
                    }
                    .title {
                        padding-left: 80px;
                        @include max992 {
                            padding-left: 60px;
                        }
                        @include max768 {
                            padding-left: 0;
                        }
                        h1 {
                            font-weight: 600;
                            font-size: 56px;
                            line-height: 60px;
                            letter-spacing: -0.6px;
                            color: #231F20;
                            margin-bottom: 12px;
                            max-width: 320px;
                            @include max768 {
                                font-weight: 600;
                                font-size: 36px;
                                line-height: 45px;
                            }
                            @include max576 {
                                max-width: 250px;
                            }
                        }
                        p {
                            font-weight: 400;
                            font-size: 20px;
                            line-height: 60px;
                            letter-spacing: -0.6px;
                            color: #7F879E;
                            @include max768 {
                                font-size: 15px;
                                line-height: 19px;
                            }
                            @include max576 {
                                max-width: 220px;
                            }
                        }
                    }
                    .h1 {
                        margin-bottom: 16px;
                    }
                    .list {
                        p {
                            display: flex;
                            align-items: center;
                            color: #7F879E;
                            @include max576 {
                                align-items: start;
                                span {
                                    font-size: 14px;
                                    line-height: 160%;
                                }
                            }
                            svg {
                                margin-right: 9px;
                                flex: 0 0 15px;
                                @include max576 {
                                    margin-top: 3px;
                                }
                            }
                            &:not(:last-child) {
                                margin-bottom: 12px;
                            }
                        }
                    }
                    .btn {
                        margin-top: 60px;
                        width: 128px;
                        height: 44px;
                        font-size: 12px;
                        @include max576 {
                            width: 100%;
                            margin-top: 100px;
                        }
                    }
                }
            }
            .test-page {
                padding:40px 26.5px 80px;
                min-height: 686px;
                display: grid;
                grid-template-columns: 3fr 1fr;
                grid-gap: 22px;
                @include max768 {
                    display: block;
                    padding: 0;
                    &>*:not(:last-child) {
                        margin-bottom: 14px;
                    }
                }
                @include max576 {
                    grid-gap: 14px;
                }
                .test-content {
                    background: #FFFFFF;
                    border-radius: 12px;
                    grid-column: 1 / 2;
                    padding: 36px 24px;
                    color: #7F879E;
                    @include max768 {
                        grid-column: unset;
                        background: unset;
                        border-top-left-radius: 0;
                        border-top-right-radius: 0;
                        padding: 0;
                        display: flex;
                        flex-direction: column;
                        min-height: 50vh;
                    }
                    .h1 {
                        margin: 11px 0;
                        color: #1B2124;
                        @include max576 {
                            margin: 4px 0;
                        }
                    }
                    &__question {
                        &-item {
                            background: #F9F9F9;
                            border-radius: 12px;
                            height: 48px;
                            display: flex;
                            align-items: center;
                            padding: 0 20px;
                            color: $grey1;
                            &:not(:last-child) {
                                margin-bottom: 12px;
                            }
                            &.active {
                                color: #252A3F;
                            }
                            @include max768 {
                                background: white;
                            }
                        }
                        &-inner {
                            padding-top: 24px;
                            @include max576 {
                                padding-top: 20px;
                            }
                        }
                        .record-area {
                            width: 100%;
                            height: 372px;
                            overflow: hidden;
                            border-radius: 20px;
                            margin-bottom: auto;
                            img {
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                            }
                            @include max576 {
                                height: 325px;
                            }
                        }
                        .btn {
                            margin-top: 32px;
                            height: 46px;
                            width: fit-content;
                            padding: 0 21px;
                            margin-left: auto;
                            margin-right: auto;
                            &.btn-red {
                                background: #EE5566;
                                @include max576 {
                                    font-size: 12px;
                                }
                            }
                        }
                    }
                    .checkbox-item {
                        @include checkbox;
                    }
                }
                .test-timer {
                    grid-column: 2 / 3;
                    background: #FFFFFF;
                    border-radius: 12px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    padding: 36px 0 46px;
                    height: fit-content;
                    @include max768 {
                        grid-column: unset;
                    }
                    @include max576 {
                        padding: 10px 0;
                    }
                    &__inner {
                        font-weight: 300;
                        font-size: 64px;
                        line-height: 150%;
                        color: #47D18C;
                        margin-top: 7px;
                        @include max992 {
                            font-size: 50px;
                            line-height: 130%;
                            margin-top: 0;
                        }
                        @include max576 {
                            font-size: 36px;
                            line-height: 122%;
                        }
                    }
                    .h2 {
                        font-size: 14px;
                    }
                }
                .test-nav {
                    grid-column: 1 / 2;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    @include max768 {
                        grid-column: unset;
                        margin-top: auto;
                    }
                    .btn-row {
                        display: grid;
                        grid-template-columns: 128px 128px;
                        grid-gap: 12px;
                        @include max576 {
                            grid-template-columns: 48px 48px;
                            grid-gap: 8px;
                            span {
                                display: none;
                            }
                            svg {
                                width: 16px;
                                height: auto;
                            }
                        }
                    }
                    .btn {
                        font-weight: 600;
                        font-size: 12px;
                        height: 44px;
                        line-height: 130%;
                        &.btn-back {
                            svg {
                                margin-right: 8px;
                                @include max576 {
                                    margin: 0;
                                }
                            }
                        }
                        &.btn-next {
                            svg {
                                margin-left: 8px;
                                @include max576 {
                                    margin: 0;
                                }
                            }
                        }
                    }
                    .btn-exit {
                        width: 128px;
                        flex: 0 0 128px;
                        color: #7F879E;
                    }
                }
                
            }
            .test-final {
                padding: 40px 26.5px 80px;
                min-height: 686px;
                display: grid;
                grid-template-columns: 3fr minmax(312px, 1fr);
                grid-gap: 22px;
                @include max992 {
                    grid-template-columns: 1fr;
                    padding: 0;
                }
                &__content {
                    height: 100%;
                    padding: 48px 50px;
                    background: #FFFFFF;
                    border-radius: 12px;
                    @include max992 {
                        padding: 0;
                        background: unset;
                        table {
                            display: none;
                        }
                    }
                    h2 {
                        font-size: 40px;
                        margin-bottom: 20px;
                        @include max768 {
                            font-size: 36px;
                            margin-bottom: 15px;
                        }
                    }
                    p {
                        color: #7F879E;
                    }
                    table {
                        margin-top: 60px;
                    }
                }
                &__result {
                    height: 100%;
                    padding: 40px 30px;
                    background: #FFFFFF;
                    border-radius: 12px;
                    display: flex;
                    flex-direction: column;
                    table {
                        margin-top: 30px;
                        td:first-child {
                            width: 150px;
                            @include max992 {
                                width: unset;
                            }
                        }
                    }
                }
                table {
                    width: 100%;
                    border-collapse: collapse;
                    font-size: 18px;
                    line-height: 150%;
                    @include max768 {
                        font-size: 16px;
                    }
                    td {
                        padding-bottom: 10px;
                        &:last-child {
                            font-weight: 600;
                        }
                    }
                    tr.mobile {
                        display: none;
                        @include max992 {
                            display: table-row;
                        }
                    }
                }
            
                .pie {
                    position: relative;
                    text-align: center;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    @include max576 {
                        img {
                            width: 135px;
                        }
                    }
                    .info {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%);
                        .percent {
                            font-weight: 300;
                            font-size: 32px;
                            line-height: 150%;
                            color: #252A3F;
                        }
                    }
                }
                .btn {
                    margin-top: auto;
                    font-size: 12px;
                    height: 44px;
                    width: 100%;
                    color: #1B2124;
                    @include max992 {
                        margin-top: 30px;
                    }
                }
            }
        }
    }

    .badge {
        @include badge(#268D61);

        &.orange {
            @include badge(#F59A74);
        }
    }

    .select-badge {
        &.is-hiring {
            margin-left: auto;
            @include badgeSelect(#268D61);
        }
        &.is-hidden {
            margin-left: auto;
            @include badgeSelect(#F59A74);
        }
        &.is-available {
            margin-left: auto;
            @include badgeSelect(#268D61);
        }
    }

    .burger {
        display: none;
        margin-right: 15px;
        @include burger;

        @include max992 {
            display: block;
        }
    }


    .tooltip {
        cursor: pointer;
        position: relative;
        &:hover {
            .tooltip-inner {
                display: flex;
            }
        }
        &-inner {
            display: none;
            transition: all .3s ease;
            width: 178px;
            height: fit-content;
            z-index: 2;
            background: #1B2124;
            border-radius: 8px;
            top: calc(100% + 5px);
            font-weight: 600;
            left: 50%;
            transform: translateX(-50%);
            font-size: 12px;
            line-height: 160%;
            justify-content: center;
            color: #FFFFFF;
            position: absolute;
            padding: 14px;
            white-space: initial;
            .text {
                color: white !important;
            }
            @include max768 {
                left: 0;
                transform: translateX(0);
            }
        }
    }
    .polygon {
        clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
        position: absolute;
        bottom: calc(100% - 6px);
        left: 50%;
        transform: translateX(-50%);
        width: 30px;
        height: 14px;
        background: #1B2124;
        @include max768 {
            left: 0;
            transform: translateX(0);
        }
    }
}